import React from "react";
import {withRouter,Link} from 'react-router-dom';



const Menu = () => {
  return (
    <div>
       <nav className="navbar navbar-dark ">
        <Link className="navbar-brand" >
          <img className="m-0"
            src="./imagenes_PPI/Logo2.png"
            width="50"
            height="50"
            alt="Logo"
            loading="lazy"
          />
            Metropolitan Guides
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarsExample01"
          aria-controls="navbarsExample01"
          aria-expanded="false"
          aria-label="Navegación de palanca"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarsExample01">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item active">
              <Link className="nav-link" to="/Inicio">
               Inicio
              </Link>
            </li>
            <li className="nav-item active">
              <Link className="nav-link" to="/Parques1">
                Planes
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link active"
                to="/Perfil"
                tabindex="-1"
                aria-disabled="true"
              >
               Perfil
              </Link>
            </li>
            <li className="nav-item dropdown">
            <Link
                className="nav-link active"
                to="/Objetivo"
                tabindex="-1"
                aria-disabled="true"
              >
                Cerrar sesión
              </Link>
              
            </li>
          
          </ul>
          <div class="col-md-3 text-end">
      <button type="button" class="btn btn-primary"><font style="vertical-align: inherit;"><font style="vertical-align: inherit;">Iniciar sesión</font></font></button>
      <button type="button" class="btn btn-primary"><font style="vertical-align: inherit;"><font style="vertical-align: inherit;">Registrarse</font></font></button>
    </div>
        </div>
      </nav>

    </div>
  );
};
export default withRouter(Menu);

